
button {
  margin: 0 10px;
  -webkit-transition: background-color .2s, color .2s;
  transition: background-color .2s, color .2s;
}
button:before {
    content: "";
    display: inline-block;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
    margin: -1px 10px;
}
button.fb {
    color: #3b5998 !important;
    border-color: #3b5998;
}
button.fb:hover {
      color: #fff !important;
      background-color: #3b5998 !important;
}
button.fb:hover:before {
        background-color: #fff;
}
button.fb:before {
      -webkit-mask-image: url(/static/facebook.svg);
              mask-image: url(/static/facebook.svg);
      background-color: #3b5998;
}
button.tw {
    color: #1dcaff !important;
    border-color: #1dcaff;
}
button.tw:hover {
      color: #fff !important;
      background-color: #1dcaff !important;
      border-color: #1dcaff !important;
}
button.tw:hover:before {
        background-color: #fff;
}
button.tw:before {
      -webkit-mask-image: url(/static/twitter.svg);
              mask-image: url(/static/twitter.svg);
      background-color: #1dcaff;
}



